.btn{
    font-size: 16px;
    padding:10px 40px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin: 20px;
    text-align:center;
    border: none;
    background-size: 300% 100%;

    border-radius: 50px;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
    background-image: -webkit-linear-gradient(left, #25aae1, #40e495, #30dd8a, #2bb673);
    background-image: linear-gradient(to right, #25aae1, #40e495, #30dd8a, #2bb673);
    box-shadow: 0 4px 15px 0 rgba(49, 196, 190, 0.75);
}
.btn:hover{
    background-position: 100% 0;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}
.btn:focus{
    outline: none;
}
.btn.color-1 {
    background-image: -webkit-linear-gradient(left, #25aae1, #40e495, #30dd8a, #2bb673);
    background-image: linear-gradient(to right, #25aae1, #40e495, #30dd8a, #2bb673);
    box-shadow: 0 4px 15px 0 rgba(49, 196, 190, 0.75);
}
.btn.color-2 {
    background-image: -webkit-linear-gradient(left, #25aae1, #4481eb, #04befe, #3f86ed);
    background-image: linear-gradient(to right, #25aae1, #4481eb, #04befe, #3f86ed);
    box-shadow: 0 4px 15px 0 rgba(65, 132, 234, 0.75);
}
.btn.color-3 {
    background-image: -webkit-linear-gradient(left, #f5ce62, #e43603, #fa7199, #e85a19);
    background-image: linear-gradient(to right, #f5ce62, #e43603, #fa7199, #e85a19);
    box-shadow: 0 4px 15px 0 rgba(229, 66, 10, 0.75);
}
.btn.color-4 {
    background-image: -webkit-linear-gradient(left, #eb3941, #f15e64, #e14e53, #e2373f);
    background-image: linear-gradient(to right, #eb3941, #f15e64, #e14e53, #e2373f);  box-shadow: 0 5px 15px rgba(242, 97, 103, .4);
}
