div.template-2 {
    font-family: "Adobe Caslon Pro", "Minion Pro", serif;
    font-size: 12pt;
    width:100%;
    max-width: 800px;

}

div.template-2 header {
    font-family: "Trajan Pro", serif;
    padding-bottom: 10px;
}

div.template-2 header h1 {
    font-size: 20pt;
    letter-spacing: 2pt;
    border-bottom: 1px solid black;
    margin-bottom: 4px;
}

div.template-2 header span {
    font-size: 10pt;
    float: right;
}

div.template-2 section h2 {
    font-family: "Trajan Pro", serif;
    font-size: 14pt;
}

div.template-2 section p {
    padding-left: 40px;
    box-sizing: border-box;
}

div.template-2 section.coverletter {
    margin-top: 40px;
}

div.template-2 section.coverletter p {
    margin-left: 0px;
}

div.template-2 section ul {
    list-style-type: circle;
}

div.template-2 .jobtable {
    display: table;
    width: 100%;
    border-bottom: 1px solid black;
    padding-left: 20px;
    box-sizing: border-box;

}

div.template-2 .edtable {
    display: table;
    width: 100%;
    padding-left: 20px;
    padding-bottom: 15px;
    box-sizing: border-box;

}

div.template-2 .skillstable {
    display: table;
