}

div.template-2 .table {
    display: table;
    padding-left: 20px;
    box-sizing: border-box;

}

div.template-2 .tablerow {
    display: table-row;
}

div.template-2 .jobtitle {
    display: table-cell;
    font-style: italic;
}

div.template-2 .right {
    display: table-cell;
    text-align: right;
}

div.template-2 .cell {
