div.template-edit{
    display: flex;
    justify-content: space-around;
}
div.template-edit div.left-half{
    width: 40vw;
    padding:10px 20px;
    box-sizing: border-box;
    display: flex;
    flex-flow: column;
    align-items: center;
    height: 95vh;
    overflow-y: scroll;
}
div.template-edit div.left-half .section-list{
    width: 80%;
}
div.template-edit div.right-half{
    width: 58vw;
    height: 95vh;
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.194);
    padding: 10px;
    box-sizing: border-box;
    overflow-y: scroll;
}
div.template-parent div.template-view{
    width: 58vw;
    padding: 20px;
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.194);
    display: flex;
    justify-content: center;
}
div.template-parent{
    width: 100vw;
    padding: 20px;
    display: flex;
    justify-content: center;
}