div.signup-form form input{
    display: block;
    width: 50%;
    margin: 5px;
    border:1px solid #eee;
    border-radius: 4px;
    height: 20px;
}
div.signup-form{
   display: flex;
   justify-content: center;
   align-items: center;
   flex-flow: column;
   height: 100vh;
}
div.signup-form form{
    width: 50%;
    height: 50%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
   background-color: #eee;


}